.sendForm {
    display: flex;
    width: 100%;
    margin: 75px 0 0;
    height: 40px;
}

.textarea {
    height: 100%;
    display: block;
    flex: 1;
    border-radius: 20px;
    resize: none;
    padding: 12px 24px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border: 0;
}

.button {
    width: 120px;
    height: 100%;
    background: #0066CC;
    border-radius: 20px;
    text-align: center;
    color: #FFFFFF;
    padding: 10px 0;
    outline: 0;
    border: 0;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    margin-left: 44px;
}

